import React from 'react';
import {Link} from 'react-router-dom'

const Nav=()=>{
    return(
        <div className="ui two column grid" style={{marginTop:"1vh"}}>
                    <div className="column">
                        <Link to="/" style={{justifySelf:"center"}}><button className="ui green button" style={{width:"100%"}}>Search Pokemon</button></Link>
                    </div>
                    <div className="column">
                        <Link to="mypokemons" style={{justifySelf:"center"}}><button className="ui green button" style={{width:"100%"}}>My Pokemons Lists</button></Link>
                    </div>
                </div>
    );
}

export default Nav;