import React from 'react';
import Card from './components/Card';
import { connect } from 'react-redux';
import {selectPokemon, displayCatch} from './redux/actions';
import Nav from './components/Nav';

class MyPokemonLists extends React.Component{
  state = {pokemons:[]};
  componentDidMount(){
      const _mypokemons=JSON.parse(localStorage.getItem("mypokemons"));
      if(_mypokemons){
        if(_mypokemons.length>0){
          this.setState({pokemons:_mypokemons});
        }
      }
  }
  render(){
      if(this.state.pokemons.length >0){
          return(
            <React.Fragment>
              <div className="container-fluid" style={{textAlign:"center",justifyContent:"center",paddingTop:"3vh"}}>
                <img alt="logo" src="/simplepokemon/pokemon/pokemon.svg" style={{width:"200px"}} />
              </div>
              <Nav />
              <div className="ui five column grid" style={{padding:"2em"}}>
                {this.state.pokemons.map((pokemon,i)=>{
                    return <Card select={()=>{
                      this.props.selectPokemon(pokemon);
                      this.props.displayCatch("none");
                    }} pokemon={pokemon} key={i} link="/details"/>;
                })}
              </div>
              <p style={{width:"100vw",textAlign:"center"}}>by: Ihsan Fajar Ramadhan</p>
            </React.Fragment>
          );
        }else{
          return(
            <React.Fragment>
              <div className="ui" style={{textAlign:"center",justifyContent:"center",paddingTop:"3vh"}}>
                <img alt="logo" src="https://seeklogo.com/images/P/Pokemon-logo-497D61B223-seeklogo.com.png" />
                <div className="ui tag green label">My Pokemon Lists</div>
              </div>
              <Nav />
              <div className="ui" style={{textAlign:"center",marginTop:"20vh",justifyContent:"center"}}>
                <i className="folder open outline icon massive blue"></i>
                <p className="ui text blue">You have no pokemon...</p>
              </div>
              <p style={{width:"100vw",textAlign:"center"}}>by: Ihsan Fajar Ramadhan</p>
            </React.Fragment>
          );
        }
  }
}

const mapStateToProps = (state) =>{
    return { 
        pokemons: state.MyPokemons
     }
}

export default connect(mapStateToProps, {selectPokemon,displayCatch})(MyPokemonLists);