import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter as Router, Route } from 'react-router-dom';
import { Provider } from 'react-redux';
import { createStore } from 'redux';
import PokemonLists from './PokemonLists';
import PokemonDetails from './PokemonDetails';
import MyPokemonLists from './MyPokemonLists';
import reducers from './redux/reducers';

const Index=()=>{
  return (
      <Router>
        <Provider store={createStore(reducers)}>
          <Route path="/" exact component={ PokemonLists } />
          <Route path="/details" component={ PokemonDetails } />
          <Route path="/mypokemons" component={ MyPokemonLists } />
        </Provider>
      </Router>
  );
}

ReactDOM.render(<Index />, document.getElementById("root"))