import { combineReducers } from 'redux';

const pokemonsReducer=(pokemons=[],action)=>{
    if(action.type === "POKEMON_ADDED"){
        return [...pokemons,action.payload];
    }
    return pokemons;
};

const selectedPokemonReducer=(selectedPokemon={},action)=>{
    if (action.type === 'POKEMON_SELECTED'){
        return action.payload;
    }
    return selectedPokemon;
};

const MyPokemonsReducer=(MyPokemons=[],action)=>{
    if (action.type === 'POKEMON_CATCHED'){
        return [...MyPokemons,action.payload];
    }
    return MyPokemons;
};

const CatchReducer=(Catch="none",action)=>{
    if (action.type === 'DISPLAY_CATCH'){
        return action.payload;
    }
    return Catch;
};

export default combineReducers({
    pokemonLists: pokemonsReducer,
    selectedPokemon: selectedPokemonReducer,
    MyPokemons: MyPokemonsReducer,
    Catch: CatchReducer
});

