import React from 'react';
import { Link } from 'react-router-dom';

const Card=(props)=>{
    const pokemon=props.pokemon;
    return (
        <div className="column">
            <div className="ui fluid card" style={{position:"relative"}}>
                <h1 style={{position:"absolute",marginLeft:"0.5vw"}}>{pokemon.id}</h1>
                <Link onClick={props.select} className="image" to="/details">
                    <img alt="nama" src={pokemon.sprites.front_default} />
                </Link>
                <div className="content" style={{textAlign:"center"}}>
                    <Link onClick={props.select} className="header" to={props.link}>{pokemon.species.name} </Link>
                </div>
            </div>
        </div>
    );
}

export default Card;