import React from 'react';
import api from './components/api';
import Card from './components/Card';
import { connect } from 'react-redux';
import { addPokemon, selectPokemon, displayCatch } from './redux/actions';
import Nav from './components/Nav';

function shuffleArray(array) {
    for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
    }
    return array;
}
class PokemonLists extends React.Component{
  state = {pokemon:[], errorMsg:''};
  test=(a)=>{
      console.log(a);
  };
  async loadApi(){
    let temp= await api.get('',{
      params:{
        limit:20,
        offset:Math.floor((Math.random() * 1000) + 1)
      }
    });
    const shuffled=shuffleArray(temp.data.results);
    for(let i=0; i<shuffled.length; i++){
      await api.get('/'+shuffled[i].name)
      .then(res => {
            this.setState({pokemon:[...this.state.pokemon, res.data]});
        });
    }
  }
  componentDidMount(){
    this.loadApi();
  }
  render(){
      if(this.state.pokemon.length >0){
          return(
            <React.Fragment>
              <div className="container-fluid" style={{textAlign:"center",justifyContent:"center",paddingTop:"3vh"}}>
              <img alt="logo" src="/simplepokemon/pokemon/pokemon.svg" style={{width:"200px"}} />
              </div>
              <Nav />
              <div className="ui five column grid" style={{padding:"2em",marginTop:"5vh"}}>
                {this.state.pokemon.map((pokemon,i)=>{
                    return <Card pokemon={pokemon} select={()=>{
                      this.props.selectPokemon(pokemon);
                      this.props.displayCatch("block");}} key={i} link="/pokemon/#/details" />;
                })}
              </div>
              <p style={{width:"100vw",textAlign:"center"}}>by: Ihsan Fajar Ramadhan</p>
            </React.Fragment>
          );
        }else{
          return(
            <React.Fragment>
              <div className="ui" style={{textAlign:"center",justifyContent:"center",paddingTop:"3vh"}}>
              <img alt="logo" src="/simplepokemon/pokemon/pokemon.svg" style={{width:"200px"}} />
              </div>
              <Nav />
              <div className="ui" style={{textAlign:"center",justifyContent:"center",marginTop:"20vh"}}>
                <i className="life ring outline loading icon massive blue"></i>
                <p className="ui text blue">Searching for nearby pokemons...</p>
              </div>
              <p style={{width:"100vw",textAlign:"center"}}>by: Ihsan Fajar Ramadhan</p>
            </React.Fragment>
          );
        }
  }
}

const mapStateToProps = (state) =>{
    return { 
        pokemons: state.pokemonLists
     }
}

export default connect(mapStateToProps, {
    addPokemon,
    selectPokemon,
    displayCatch
})(PokemonLists);