import React from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { catchPokemon } from './redux/actions';
import Card from './components/Card';
import Nav from './components/Nav';

class PokemonDetails extends React.Component{
    state={redirect:false};
    Catch=()=>{
        const result= Math.round(Math.random());
        if(result===1){
            this.props.catchPokemon(this.props.pokemon);
            let _pokemon=this.props.pokemon;
            let _mypokemons=localStorage.getItem("mypokemons");
            if(_mypokemons){
                if(JSON.parse(_mypokemons).length >0){
                    _mypokemons=JSON.parse(_mypokemons);
                    _mypokemons=[this.props.pokemon,..._mypokemons];
                    localStorage.setItem("mypokemons", JSON.stringify(_mypokemons));
                }
            }else{
                console.log("new");
                localStorage.setItem("mypokemons", JSON.stringify([_pokemon]));
            }
            alert("Catched!");
        }else{
            alert("Failed!");
        }
        this.setState({redirect:true});
    }
    render(){
        if(this.state.redirect){
            return <Redirect to="mypokemons" />
        }else if(this.props.pokemon.id){
            return(
                <>
                <div className="container-fluid" style={{textAlign:"center",justifyContent:"center",paddingTop:"3vh"}}>
                <img alt="logo" src="/simplepokemon/pokemon/pokemon.svg" style={{width:"200px"}} />
                </div>
                <Nav />
                <button onClick={this.Catch} className="ui yellow button" style={{display:this.props.displayCatch,justifySelf:"center",width:"100%",marginTop:"5vh"}}>Catch!</button>
                <div className="ui two column grid" style={{padding:"2em"}}>
                    <div className="ui two column grid">
                        <div className="column"></div>
                        <Card pokemon={this.props.pokemon} link="/details" />
                    </div>
                    <div className="column">
                        <div className="ui list">
                            {this.props.pokemon.stats.map((e,i)=>{
                            return (
                            <div className="item" key={i}>
                                <span className="header">{e.stat.name.toUpperCase()}</span>
                                <div className="ui progress green" data-value={e.base_stat} id="example5">
                                    <div className="bar" style={{width:Math.floor(e.base_stat/300*100)+"%"}}>
                                        <div className="progress">{e.base_stat}</div>
                                    </div>
                                </div>
                            </div>);
                            })}
                        </div>
                    </div>
                </div>
                <p style={{width:"100vw",textAlign:"center"}}>by: Ihsan Fajar Ramadhan</p>
                </>            
            );
        }else{
            return <div>nothing is selected</div>;
        }
    };
}

const getState=(state)=>{
    return {
        pokemon:state.selectedPokemon,
        mypokemons:state.MyPokemons,
        displayCatch:state.Catch}
}

export default connect(getState, {catchPokemon})(PokemonDetails);