export const addPokemon = pokemon =>{
    return{
        type: 'POKEMON_ADDED',
        payload: pokemon
    }
}

export const selectPokemon = pokemon =>{
    return{
        type: 'POKEMON_SELECTED',
        payload: pokemon
    };
}

export const catchPokemon = pokemon =>{
    return{
        type: 'POKEMON_CATCHED',
        payload: pokemon
    }
}

export const displayCatch = display =>{
    return{
        type: 'DISPLAY_CATCH',
        payload: display
    }
}